body {
  padding: 2rem;
  font-family: Arial, Helvetica, sans-serif;
}

.App {
  display: flex;
  flex-direction: column;
  max-width: 40rem;
  margin: 0 auto;
  min-height: calc(100vh + 10rem);
}

.content {
  flex: 1;
}

img {
  max-width: 100%;
  border-radius: 0.5rem;
}

a {
  text-decoration: none;
  color: #121212;
}

h1 {
  font-size: 1.2rem;
}

.animeLeft {
  transform: translateX(-20px);
  opacity: 0;
  animation: animeLeft 0.3s forwards;
}
@keyframes animeLeft {
  to {
    transform: initial;
    opacity: initial;
  }
}

@keyframes loadingAnime {
  to {
    transform: rotate(360deg);
  }
}
.loading {
  animation: loadingAnime 1s infinite reverse linear;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  border: 10px solid #ccc;
  border-right-color: transparent;
}
